<template>
    <div class="container">
        <div class="main__todos" v-if="products">
            <div class="main__todos-item" v-for="prodcut in products" key:product.id>
                <p>Id: {{ prodcut.id }}</p>
                <p>Title: {{ prodcut.title }}</p>
                <p>Completed: {{ prodcut.completed }}</p>
            </div>           
        </div> 
        <div class="main__section" v-else> Plase wait...</div>
    </div>
</template>

<script>
    export default {
        data(){
        return {
                products: null
            }
        },
        created() {
            this.getProducts()
        },
        methods:{
            async getProducts(){
            let response = await fetch ('https://jsonplaceholder.typicode.com/todos')
            let data = await response.json()
            this.products = data
            }
        }
    }
</script>